/*Imports*/
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@1,700&family=Roboto+Slab:wght@400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ------------------------ */
/* GENERAL STYLES */
/* ------------------------ */

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Inter", sans-serif;
  color: #343a40;
  margin: 0;
  padding: 0;
}

.container {
  width: 960px;
  margin: 0 auto;
}

.navbar {
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 20%;
  width: 960px;
  padding: 12px 10px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
  z-index: 999;
}

.navbar .Logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2px;
}

.navbar .Logo img {
  width: 20px;
  height: 25px;
}

.navbar .logo_name {
  margin-bottom: 0;
  font-family: "Lora", serif;
  font-size: x-large;
  color: #087f5b;
}

.navbar-list {
  list-style: none;
  display: flex;
  justify-content: center;
}
.navbar-list li {
  padding: 0 15px;
}
.navbar-list li a {
  text-decoration: none;
  color: #1b1b1b;
  font-weight: bold;
  transition: color 0.3s;
}

.navbar-list li a:hover {
  color: #099268;
}

.menu-icon {
  display: none;
  cursor: pointer;
}

.bar {
  width: 30px;
  height: 3px;
  background-color: #333;
  margin: 6px 0;
  transition: transform 0.3s;
  z-index: 999;
}

.bar.closed:nth-child(1) {
  transform: translateY(4px) rotate(45deg);
}

.bar.closed:nth-child(2) {
  transform: scaleX(0);
}

.bar.closed:nth-child(3) {
  transform: translateY(-14px) rotate(-45deg);
}

.fullscreen-menu {
  position: fixed;
  top: 0;
  right: 100%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: right 0.3s;
  z-index: 999;
}

.fullscreen-menu-list {
  list-style: none;
  padding: 0;
  text-align: center;
}

.fullscreen-menu-list li {
  margin: 20px 0;
}

.fullscreen-menu-list li a {
  color: #fff;
  font-size: 24px;
  text-decoration: none;
}

header,
section {
  margin-bottom: 96px;
  margin-top: 76px;
}

h2 {
  margin-bottom: 48px;
  font-size: 36px;
  letter-spacing: -0.5px;
}

.grid-3-cols {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 80px;
}

.btn:link,
.btn:visited {
  background-color: #087f5b;
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  display: inline-block;
  border-radius: 100px;
}

.btn:hover,
.btn:active {
  background-color: #099268;
}

.btn--big {
  font-size: 18px;
  padding: 16px 32px;
}

.btn--small {
  font-size: 14px;
  padding: 8px 12px;
}

img {
  border-radius: 12px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

/* ------------------------ */
/* COMPONENT STYLES */
/* ------------------------ */

/* HEADER */
header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 80px;
}

.header-text-box {
  align-self: center;
}

h1 {
  margin-bottom: 32px;
  font-size: 44px;
  line-height: 1.1;
  letter-spacing: -1px;
  /* 44 / 52 / 62 */

  /* text-shadow: 0 5px 5px rgba(0, 0, 0, 0.2); */
}

.header-text {
  margin-bottom: 24px;
  font-size: 18px;
  line-height: 1.7;
}

img {
  width: 100%;
}

/* FEATURES */
.features-icon {
  stroke: #087f5b;
  width: 32px;
  height: 32px;
  margin-bottom: 24px;
}

.features-title {
  margin-bottom: 16px;
  font-size: 20px;
}

.features-text {
  font-size: 18px;
  line-height: 1.7;
}

/* TESTIMONIAL */
.testimonial-section {
  background-color: rgb(9, 9, 9);
  color: #fff;
  padding: 24px;
  border-radius: 12px;
}

.testimonial-box {
  grid-column: 2 / -1;
  align-self: center;
}

.testimonial-box h2 {
  margin-bottom: 24px;
  /* 20 / 24 / 30 */
  font-size: 24px;
}

.testimonial-text {
  font-style: italic;
  margin-bottom: 24px;
  font-size: 18px;
  line-height: 1.7;
  color: #e6fcf5;
}

.testimonial-author {
  color: #c3fae8;
}

/* PLACES */
.place {
  box-shadow: 0 20px 30px 0 rgba(0, 0, 0, 0.07);
  border-radius: 12px;
  max-height: 580px;
}

.place img {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  max-height: 280px;
}

.place-box {
  padding: 20px;
}

h3 {
  margin-bottom: 24px;
  font-size: 20px;
}

.place-description {
  list-style: none;
  margin-bottom: 40px;
}

footer {
  margin-bottom: 48px;
  text-align: center;
  font-size: 14px;
  color: #495057;
}

/* Mobile Phones (Portrait) Breakpoint: 320px */
@media (max-width: 480px) {
  .navbar {
    left: 0;
    max-width: 100vw;
  }
  .menu-icon {
    display: block;
  }
  .navbar-list {
    display: none;
  }
}

/*Mobile Phones (Landscape) Breakpoint: 480px */
@media (max-width: 480px) and (orientation: landscape) {
  .navbar {
    left: 0;
    max-width: 100vw;
  }
  .menu-icon {
    display: block;
  }
  .navbar-list {
    display: none;
  }
}

/*Tablets (Portrait) Breakpoint: 768px*/

@media (min-width: 481px) and (max-width: 768px) {
  .navbar {
    left: 0;
    max-width: 100vw;
  }
  .menu-icon {
    display: block;
  }
  .navbar-list {
    display: none;
  }
}

/*Tablets (Landscape)  Breakpoint: 1024px*/

@media (min-width: 769px) and (max-width: 1024px) {
  .navbar {
    left: 0;
    max-width: 100vw;
  }
  .menu-icon {
    display: block;
  }
  .navbar-list {
    display: none;
  }
}

